import { DatePipe } from '@angular/common';
import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { PsAccountsListComponent } from 'src/app/commonBussinessSRC/psComponents/ps-accounts-list/ps-accounts-list.component';
import { IOptionsPsAccountsListExposed } from 'src/app/commonBussinessSRC/psComponents/ps-accounts-list/ps-accounts-list.component.interfaces';
import { IOptionsPsComplexAccountTypesExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-account-types/ps-complex-account-types.component.interface';
import { IOptionsPsComplexAmountExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-amount/ps-complex-amount.component.interfaces';
import { IOptionsPsLookupOwnAccountsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-own-accounts/ps-lookup-own-accounts.component.interfaces';
import { IOptionsPsMapAtmBranchesExposed } from 'src/app/commonBussinessSRC/psComponents/ps-map-atm-branches/ps-map-atm-branches.component.interfaces';
import { IOptionsAccountOpening } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { EventEmitterService } from 'src/app/commonSRC/psServices/event-emitter/event-emitter.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IMapKeyValue, IOptionsTemplateStepper } from '../../commonSRC/psServices/models/ps-common-interface';
import { OmniBasePage } from '../omni-base/omni-base.page';
import { PsCurrencyPipe } from './../../commonSRC/psPipes/ps-currency/ps-currency.pipe';
import { IchangeValues, IOptionsPsContainerPanel } from './../../commonSRC/psServices/models/ps-common-interface';


@Component({
  selector: 'app-change-maturity-instructions',
  templateUrl: './change-maturity-instructions.page.html',
  styleUrls: ['./change-maturity-instructions.page.scss'],
})
export class ChangeMaturityInstructionsPage extends OmniBasePage implements OnInit, AfterViewInit {
  formGroup: FormGroup = new FormGroup({});
  changeMaturityInstructionsVO: any = {};
  stepperOptions: IOptionsTemplateStepper = {
    isHorizontalStepper: true,
    requestObject:this.changeMaturityInstructionsVO,
    numberOfSteps: 1,
    namesofSteps: ['change_maturity_step1'],
    group: this.formGroup,
    submitOptions: {
      extraParams: {},
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.updateMaturityInstructions,
      group: this.formGroup,
      //Mohanad - AZDB-4356 - 02102024
      preCallFunction: {
        func(response) {
          return new Promise<any>((resolve, reject) => {
            resolve(this.executionClass.checkProfitAccount());
          });
        },
        executionClass: this,
        params: [this]
      },
      //End
      postCallFunction: {
        func(response) {
          return new Promise<any>((resolve, reject) => {
            resolve(this.executionClass.serviceRes(response));
          });
        },
        params: [this],
        executionClass: this
      },
    }
  };
  panelOptionsStep1: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'maturity_instructions_key',
    iconName: 'document',
    expanded: true
  };
  panelOptionsStep2: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'account_details_key',
    iconName: 'document',
    expanded: true
  };
  @ViewChild('maturitrytransferRef') maturitrytransferRef;
  @ViewChild('profitAccountRef') profitAccountRef;
  private psCurrency = new PsCurrencyPipe();
  private accountAsIBAN: any;
  public generalLabelValuesMap = new Map<string, IMapKeyValue>();
  public generalHeaderMap = new Map<string, IMapKeyValue>();
  public termsLabelValuesMap = new Map<string, IMapKeyValue>();
  public termsHeaderMap = new Map<string, IMapKeyValue>();

  enableCifBranch = false;
  enablePeriodicity = false;

  accountTypesOptions: IOptionsPsComplexAccountTypesExposed = {
    accountTypesOptions: {
      allowedAccountType: ConstantCommon.AllowedFixedMaturityAccountsTypes,
      accountCategory: ConstantCommon.ACC_TYPE_T,
      group: this.formGroup,
      fcName: 'accountTypes'
    },
    periodicity: {
      group: this.formGroup,
      fcName: 'periodiCity'
    },
    minimumBalance: {
      group: this.formGroup,
      fcName: 'minBalance'
    },
    group: this.formGroup,
    requestObject: this.changeMaturityInstructionsVO
  };

  psComplexAmountOptions: IOptionsPsComplexAmountExposed = {
    currenciesOptions: {
      labelKey: 'currency_key',
      placeHolder: 'currency_key',
      group: this.formGroup,
      fcName: 'currency'
    },
    amountOptions: {
      labelKey: 'opening_balance_key',
      placeHolder: 'enter_opening_balance_key',
      fcName: 'inputAmount',
      group: this.formGroup,
      type: 'amount',
      decimalPoints: 3
    }
  };

  public mainOptions: IOptionsAccountOpening = {
    toggleOptions: {
      labelKey: 'renew_key',
      placeHolder: 'renew_key',
      group: this.formGroup,
      fcName: 'renew',
      psClass: 'toggleColor'
    },
    toggleProfitOptions: {
      labelKey: 'Post_Profit_to_Same_Account_key',
      placeHolder: 'Post_Profit_to_Same_Account_key',
      group: this.formGroup,
      fcName: 'renewWithSame'
    }
  };

  public renewAccount 
  public postProfitAccount

  profitRate: string;

  accountListOptions: IOptionsPsAccountsListExposed = {
    group: this.formGroup,
    fcName: 'account',
    isEditable: false,
    requestObject: this.changeMaturityInstructionsVO
  };

  public maturityTransferOptions: IOptionsPsLookupOwnAccountsExposed = {
    labelKey: 'on_maturity_transfer_balance_to_key',
    placeHolder: 'select_on_maturity_transfer_balance_to_key',
    component: PsAccountsListComponent,
    group: this.formGroup,
    accountAllowedCurrencies: [],
    accountAllowedTypes: [],
    glTypes: 'G',
    fcName: 'maturityAccount',
    accountType: 'G',
    fromTo: 'to',
    requestObject: this.changeMaturityInstructionsVO
  };

  profitAccOptions: IOptionsPsLookupOwnAccountsExposed = {
    labelKey: 'post_profit_to_key',
    placeHolder: 'select_post_profit_to_key',
    component: PsAccountsListComponent,
    group: this.formGroup,
    accountAllowedCurrencies: [],
    accountAllowedTypes: [],
    glTypes: 'G',
    fcName: 'profitAccount',
    accountType: 'G',
    fromTo: 'to',
    requestObject: this.changeMaturityInstructionsVO

  };
  cifInfo: any;
  mapAtmBranchesOptions: IOptionsPsMapAtmBranchesExposed;
  termAccount:any;

  constructor(public commonProv: PsCommonService, private navService: PsNavigatorService, public loggerP: LoggerService, public eventEmitterService: EventEmitterService, private datePipe: DatePipe, private omniPull?: OmniPullService) {
    super();
  }

  // edited by Marina jira-518 to remove the account lookup and have the account card like other edit screens
  ngOnInit() {
    super.init();
    this.baseFormGroup = this.formGroup;
    this.cifInfo = this.commonProv.session.getValueOf(ConstantCommon.USERINFO);
    this.termAccount = this.navService.getAllParams() ? this.navService.getAllParams() : {}
    this.commonProv.copyObject(this.changeMaturityInstructionsVO, this.navService.getAllParams() ? this.navService.getAllParams().ScreenVO ? this.navService.getAllParams().ScreenVO : this.navService.getAllParams() : {})
    this.accountListOptions.listOfOptions = [];
    this.accountListOptions.listOfOptions.push(this.changeMaturityInstructionsVO);
    this.commonProv.copyObject(this.stepperOptions.submitOptions.extraParams, { branch: this.cifInfo.customerInfoCO ? this.cifInfo.customerInfoCO.branchCode : '' }, false, true);
  }


  ngAfterViewInit() {
    //added by ghada, AZDB-4356 to send selected Account Info as object on submit
    this.changeMaturityInstructionsVO['fromAccount'] = { ...this.changeMaturityInstructionsVO };
    setTimeout(() => {
      this.changeMaturityInstructionsVO[this.accountTypesOptions.accountTypesOptions.fcName] = this.changeMaturityInstructionsVO.accGl;
      this.changeMaturityInstructionsVO[this.psComplexAmountOptions.currenciesOptions.fcName] = this.changeMaturityInstructionsVO.currency;
    }, 300);
    if (this.navService.getAllParams()?.MaturityInfo) {
      this.commonProv.copyObject(this.changeMaturityInstructionsVO, this.navService.getAllParams().MaturityInfo);
      this.renewAccount = this.changeMaturityInstructionsVO.renew;
      this.postProfitAccount = this.renewAccount ? this.changeMaturityInstructionsVO.renewWithSame : false;
      this.setMaturityTogglesValues();
      delete this.changeMaturityInstructionsVO.MaturityInfo;
    }
  }

  serviceRes(res) {
    this.commonProv.logger.log(res);
  }

  onChange(values: IchangeValues) {
    if (values.newValue == false) {
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.profitAccOptions.fcName], 0);
      this.profitAccountRef?.reset();
      this.profitAccOptions = { ...this.profitAccOptions };//Force Object to be init to call ngOnChanges
    } else {
      this.maturitrytransferRef?.reset();
      //ADDED BY MICHEL IN ORDER TO RESET THE OLDVALUE OF THE "renewWithSame" TOGGLE IN ORDER TO RE-TRIGGER THE onProfitChange METHOD TO Reapply the visibility customization after "RENEW" is enabled for the second time
      if(this.mainOptions.toggleProfitOptions.group.controls[this.mainOptions.toggleProfitOptions.fcName]){
      this.mainOptions.toggleProfitOptions.group.controls[this.mainOptions.toggleProfitOptions.fcName]['oldValue'] = null;
      }
      this.changeMaturityInstructionsVO[this.mainOptions.toggleProfitOptions.fcName] = false;
    }
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.mainOptions.toggleProfitOptions.fcName], values.newValue == false ? 0 : 1);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.maturityTransferOptions.fcName], values.newValue == false ? 1 : 0);
    // this.changeMaturityInstructionsVO[this.mainOptions.toggleOptions.fcName] = values.newValue;//Force renew Value to new value
  }

  onProfitChange(values1: IchangeValues) {
    if (values1.newValue == false) {
      this.changeMaturityInstructionsVO[this.mainOptions.toggleOptions.fcName] = true;
    } else {
      this.profitAccountRef?.reset();
    }       
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.profitAccOptions.fcName], values1.newValue == false ? 1 : 0);
  }

  setMaturityTogglesValues() {
    this.changeMaturityInstructionsVO[this.mainOptions.toggleOptions.fcName] = this.renewAccount;
    this.changeMaturityInstructionsVO[this.mainOptions.toggleProfitOptions.fcName] = this.renewAccount ? this.postProfitAccount : undefined
  }

  //Mohanad - AZDB-4356 - 02102024
  checkProfitAccount():void{
    const isRenewed = this.stepperOptions.requestObject[this.mainOptions.toggleOptions.fcName] === true || this.stepperOptions.requestObject[this.mainOptions.toggleOptions.fcName] === "true";
    const isRenewedWithSame = this.stepperOptions.requestObject[this.mainOptions.toggleProfitOptions.fcName] === true || this.stepperOptions.requestObject[this.mainOptions.toggleProfitOptions.fcName] === "true";
    if(isRenewed && isRenewedWithSame || !isRenewed && !isRenewedWithSame ){
      this.commonProv.setValInsideNestedObj(this.profitAccOptions.fcName, this.termAccount, this.stepperOptions.requestObject);
    }
  }

}
